"use client"

import { useEffect, useRef, useState } from "react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import mapboxgl from 'mapbox-gl'
import 'mapbox-gl/dist/mapbox-gl.css'

// Configuração do Mapbox (substitua pelo seu token real)
mapboxgl.accessToken = process.env.NEXT_PUBLIC_MAPBOX_TOKEN || ''

// Tipagem para os locais
interface Location {
  name: string
  coordinates: [number, number]
  type: "Atração" | "Restaurante" | string
}

// Mock data - Substitua com dados reais da sua API
const mockLocations: Location[] = [
  {
    name: "Parque Municipal",
    coordinates: [-46.633308, -23.550520],
    type: "Atração"
  },
  {
    name: "Restaurante Típico",
    coordinates: [-46.634308, -23.551520],
    type: "Restaurante"
  },
  {
    name: "Museu de História",
    coordinates: [-46.635308, -23.552520],
    type: "Atração"
  }
]

export default function MapPage() {
  const mapContainer = useRef<HTMLDivElement>(null)
  const map = useRef<mapboxgl.Map | null>(null)
  const [selectedLocation, setSelectedLocation] = useState<Location | null>(null)

  useEffect(() => {
    if (!mapContainer.current || !mapboxgl.accessToken) return

    // Inicializa o mapa
    map.current = new mapboxgl.Map({
      container: mapContainer.current,
      style: 'mapbox://styles/mapbox/light-v11',
      center: mockLocations[0].coordinates,
      zoom: 13
    })

    // Adiciona controles de navegação
    map.current.addControl(new mapboxgl.NavigationControl(), 'top-right')

    // Adiciona marcadores
    mockLocations.forEach((location) => {
      const marker = new mapboxgl.Marker({
        color: location.type === 'Restaurante' ? '#e11d48' : '#1d4ed8'
      })
        .setLngLat(location.coordinates)
        .setPopup(new mapboxgl.Popup().setHTML(`
          <h3 class="font-bold">${location.name}</h3>
          <p class="text-sm text-gray-600">${location.type}</p>
        `))
        .addTo(map.current!)

      marker.getElement().addEventListener('click', () => {
        setSelectedLocation(location)
      })
    })

    // Desenha rota entre os pontos
    const drawRoute = () => {
      if (!map.current || mockLocations.length <= 1) return

      const coordinates = mockLocations.map(loc => loc.coordinates)
      
      if (map.current.getSource('route')) {
        (map.current.getSource('route') as mapboxgl.GeoJSONSource).setData({
          type: 'Feature',
          properties: {},
          geometry: {
            type: 'LineString',
            coordinates: coordinates
          }
        })
      } else {
        map.current.addSource('route', {
          type: 'geojson',
          data: {
            type: 'Feature',
            properties: {},
            geometry: {
              type: 'LineString',
              coordinates: coordinates
            }
          }
        })

        map.current.addLayer({
          id: 'route',
          type: 'line',
          source: 'route',
          layout: {
            'line-join': 'round',
            'line-cap': 'round'
          },
          paint: {
            'line-color': '#4b5563',
            'line-width': 3
          }
        })
      }
    }

    // Se o mapa já estiver carregado, desenha a rota imediatamente
    if (map.current.isStyleLoaded()) {
      drawRoute()
    } else {
      map.current.on('load', drawRoute)
    }

    // Cleanup
    return () => {
      map.current?.remove()
    }
  }, [])

  return (
    <div className="min-h-screen bg-white">
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4 h-screen">
        {/* Container do Mapa */}
        <div 
          className="md:col-span-2 h-[50vh] md:h-full" 
          ref={mapContainer} 
        />

        {/* Sidebar */}
        <div className="p-4 overflow-y-auto">
          <Card>
            <CardHeader>
              <CardTitle className="text-xl font-bold">
                Pontos do Roteiro
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                {mockLocations.map((location, index) => (
                  <Card 
                    key={`${location.name}-${index}`}
                    className={`cursor-pointer transition-colors ${
                      selectedLocation?.name === location.name ? 'border-red-600 border-2' : ''
                    }`}
                    onClick={() => {
                      setSelectedLocation(location)
                      map.current?.flyTo({
                        center: location.coordinates,
                        essential: true,
                        zoom: 15
                      })
                    }}
                  >
                    <CardContent className="p-4">
                      <h3 className="font-semibold">{location.name}</h3>
                      <p className="text-sm text-gray-600 capitalize">{location.type.toLowerCase()}</p>
                    </CardContent>
                  </Card>
                ))}
              </div>

              <Button 
                className="w-full mt-6 bg-red-600 hover:bg-red-700"
                onClick={() => window.location.href = "/itinerary"}
              >
                Voltar ao Roteiro
              </Button>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  )
}